:root {
  --bg: #efedf4;
  --phone: #8a6c91;
  --phone-top: #55345c;
  --phone-top-text: #d3d4d9;
  --phone-shadow: #000;
  --choose-profile-bg: #efedf4;
  --choose-profile-hover: #fa905d;
  --choose-profile-text: #37383e;
  --chat-bg: #f5f4f5;
  --chat-username: #84848f;
  --chat-text: #37383e;
  --selection-bg: #595c65;
  --selection-btn: #fff;
  --selection-btn-hover: #cfcdd4;
  --selection-text: #37383e;
  --btn: #fff;
  --btn-hover: #cfcdd4;
}

[data-theme='dark'] {
  --bg: #d3d4d9;
  --phone: #37383e;
  --phone-top: #28282b;
  --phone-top-text: #d3d4d9;
  --phone-shadow: #000;
  --choose-profile-bg: #f5f4f5;
  --choose-profile-hover: #595c65;
  --choose-profile-text: #37383e;
  --chat-bg: #f5f4f5;
  --chat-username: #84848f;
  --chat-text: #37383e;
  --selection-bg: #595c65;
  --selection-btn: #fff;
  --selection-btn-hover: #cfcdd4;
  --selection-text: #37383e;
  --btn: #fff;
  --btn-hover: #cfcdd4;
}

* {
  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box; /* Firefox, other Gecko */
  box-sizing: border-box; /* Opera/IE 8+ */
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h2 {
  font-size: 1.875em;
}

h3 {
  font-size: 1.705em;
}

h4 {
  font-size: 1.535em;
}

h5 {
  font-size: 1.365em;
}

p,
div {
  font-size: 0.875em;
}

select,
input,
button {
  font-size: 0.875em !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
