/* Toggle Switch source: https://www.geeksforgeeks.org/how-to-create-a-toggle-switch-in-react-as-a-reusable-component/ */

.toggle-container {
  position: absolute;
  top: 20px;
  right: 30px;
  display: flex;
  flex-flow: row unwrap;
  justify-content: flex-end;
  align-items: center;
  gap: 15px;
  text-align: center;
}

.toggle {
  position: relative;
  display: inline-block;
  width: 65px;
  text-align: left;
}

.toggle-checkbox {
  display: none;
}

.toggle-label {
  display: block;
  border: 0 solid #bbb;
  border-radius: 20px;
  cursor: pointer;
  overflow: hidden;
}

.toggle-inner {
  display: block;
  margin-left: -100%;
  width: 200%;
  transition: margin 0.3s ease-in 0s;
}

.toggle-inner:before,
.toggle-inner:after {
  float: left;
  box-sizing: border-box;
  padding: 0;
  width: 50%;
  height: 33px;
  background-color: var(--phone);
  color: #fff;
  font-weight: bold;
  line-height: 33px;
}

.toggle-inner:before {
  content: '';
  padding-left: 10px;
  color: #fff;
}

.toggle-inner:after {
  content: '';
  padding-right: 10px;
  color: #fff;
  text-align: right;
}

.toggle-switch {
  margin: 5px;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 30px;
  display: block;
  border: 0 solid #bbb;
  border-radius: 20px;
  width: 24px;
  background-color: #fff;
  transition: all 0.3s ease-in 0s;
}

.toggle-checkbox:checked + .toggle-label .toggle-inner {
  margin-left: 0;
}

.toggle-checkbox:checked + .toggle-label .toggle-switch {
  right: 0px;
}

@media (max-width: 620px) {
  .toggle-container {
    top: 10px;
    right: 15px;
  }

  .toggle-container h4 {
    display: none;
  }

  .toggle {
    width: 50px;
  }

  .toggle-inner:before,
  .toggle-inner:after {
    height: 30px;
    line-height: 30px;
  }

  .toggle-inner:before {
    padding-left: 5px;
  }

  .toggle-inner:after {
    padding-right: 5px;
  }

  .toggle-switch {
    margin: 5px;
    right: 20px;
    width: 20px;
  }
}
